import React , { useState , useEffect } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import stl from '../STYLES/FIND_JOB/findjob.module.scss'
import  States from '../components/SELECT_OPTIONS/state' 
import Categories from '../components/SELECT_OPTIONS/category'
import { GET_ALL_JOB_ADDS } from '../queries/index'
import { useQuery } from '@apollo/react-hooks';
import moment from 'moment';
import { navigate } from '@reach/router';


const initialState = {
  jobs:[],
  filters:{
    state:null,
    category:null
  }
}

const FindJobBody = () => {


  const { data } = useQuery(GET_ALL_JOB_ADDS);
  const [state , setState] = useState(initialState)

  useEffect(() => {
    let filteredJobs = data ? data.getAllJobAdds : []
    filteredJobs =  state.filters.state ? filteredJobs.filter(x => x.location.state === state.filters.state) : filteredJobs
    filteredJobs =  state.filters.category ? filteredJobs.filter(x => x.category === state.filters.category) : filteredJobs
    setState({...state, jobs:filteredJobs})

  },[data , state.filters])

const filetJobs = e => {
  const { name , value } = e.target
  const filters = {
  ...state.filters,
  [name]:value
  }
  setState({...state , filters})
}





  return (
    <div className = {stl.background}>
     <h1 className = {stl.h1}>FIND A JOB</h1>
     <div className = {stl.container}>
      <div style = {{marginBottom:"20px"}} className = {stl.filter}>
       <h5>FILTER</h5>
      <div  className = {stl.selectors}>
          <States name = "state" onChange = {(e) => filetJobs(e)}  />
          <Categories name = "category" onChange = {(e) => filetJobs(e)}  />
      </div>
      </div>
      <ul className = {stl.jobsList}>
      {state.jobs.length ? state.jobs.map(({language ,  _id, category, createDate, creator , title , body , location , email , phone}) => (
        <li key = {_id}>

          <h4>{title}</h4>
          <p style={{margin:"10px 0px"}}><strong>Category:</strong> {category}</p>
          <h5 className = {stl.bodyMsg}>{body}</h5>
          <p><strong>State:</strong> {" "}{location.state}</p>
          <p><strong>City:</strong> {" "}{location.city}</p>
          <p onClick = {() => navigate(`user/#/${creator.username}`)} style = {{ borderTop:"1px solid black"}}><strong>Created by:</strong> {" "}<span style = {{ borderTop:"0px", display:"inline", color:"darkBlue", textDecoration:"underline", cursor:"pointer"}}>{creator.username}</span></p>
          <p><strong>Created Date: </strong>{moment(Number(createDate)).format('MMMM Do YYYY')}</p>
          <p style = {{borderTop:"1px solid black"}}><strong>email:</strong>{email ? email : '-'}</p>
          <p><strong>phone: </strong>{phone ? phone : '-'}</p>
        </li>
      )):<p>nothing was found</p>}
      </ul>
     </div>
   </div>
  )

}

const FindJob = () => (
  <Layout>
    <SEO title="find-job" />
    <FindJobBody />
  </Layout>
)

export default FindJob